'use client';

import { useEffect, useMemo, useState } from 'react';
import Link from 'next/link';

/**
 * Crypto Dashboard (app/uis/crypto/page.js)
 *
 * - Dashboard + wallet management live here (create wallet, see wallets, balances).
 * - Shows global recent transactions (last / all toggle) and a small balances chart.
 * - Calls these endpoints (falls back to mocks if unavailable):
 *    - POST /api/crypto        -> create wallet { userId, currency } => wallet
 *    - GET  /api/crypto?userId=...  -> list wallets for user => [wallets]
 *    - GET  /api/crypto/txs?limit=N -> global transactions list => [txs]
 *
 * Replace or adapt the endpoints to match your backend if needed.
 */

const SAMPLE_COINS = [
  { id: 'bitcoin', name: 'Bitcoin (BTC)' },
  { id: 'ethereum', name: 'Ethereum (ETH)' },
  { id: 'cardano', name: 'Cardano (ADA)' }
];

function SmallBarChart({ data = [] }) {
  // data: [{label, value}]
  const width = 360;
  const height = 100;
  const max = Math.max(...data.map((d) => d.value), 1);
  const barWidth = data.length ? Math.floor(width / data.length) - 8 : 0;

  return (
    <svg width={width} height={height} style={{ background: '#fff', borderRadius: 8 }}>
      {data.map((d, i) => {
        const barH = (d.value / max) * (height - 28);
        const x = 8 + i * (barWidth + 8);
        const y = height - barH - 18;
        return (
          <g key={d.label}>
            <rect x={x} y={y} width={barWidth} height={barH} rx="4" fill="#0b74de" />
            <text x={x + barWidth / 2} y={height - 4} fontSize="10" textAnchor="middle" fill="#333">
              {d.label}
            </text>
          </g>
        );
      })}
    </svg>
  );
}

export default function CryptoIndex() {
  // keep the connected user id (hidden on this dashboard)
  const [userId, setUserId] = useState(null);
  const [wallets, setWallets] = useState([]);
  const [txs, setTxs] = useState([]);
  const [txModeAll, setTxModeAll] = useState(false);
  const [loadingWallets, setLoadingWallets] = useState(false);
  const [loadingTxs, setLoadingTxs] = useState(false);
  const [msg, setMsg] = useState(null);

  // load global wallets and transactions on mount (dashboard is global)
  useEffect(() => {
    loadWallets();
    loadTxs(100);
  }, []);

  // silently detect connected user (used only for navigating to per-id page)
  useEffect(() => {
    async function fetchCurrentUser() {
      try {
        const res = await fetch('/api/auth/me');
        if (res.ok) {
          const json = await res.json();
          const id = json?.id || json?.userId || json?.email || json?.name;
          if (id) { setUserId(String(id)); return; }
        }
      } catch (err) {
        console.warn('fetch /api/auth/me failed', err);
      }
      try {
        const res2 = await fetch('/api/user');
        if (res2.ok) {
          const j2 = await res2.json();
          const id2 = j2?.id || j2?.userId || j2?.email || j2?.name;
          if (id2) setUserId(String(id2));
        }
      } catch (e) {
        console.warn('fetch /api/user failed', e);
      }
    }
    fetchCurrentUser();
  }, []);

  async function loadWallets() {
    setLoadingWallets(true);
    setMsg(null);
    try {
      // fetch all wallets (global) for the dashboard
      const res = await fetch(`/api/crypto`);
      if (!res.ok) throw new Error('no wallets');
      const json = await res.json();
      setWallets(Array.isArray(json) ? json : [json]);
    } catch (err) {
      // fallback mock: no wallets
      console.warn('loadWallets failed, using mock', err);
      setWallets([]);
    } finally {
      setLoadingWallets(false);
    }
  }

  async function loadTxs(limit = 10) {
    setLoadingTxs(true);
    try {
      const url = `/api/crypto/txs?limit=${limit}`;
      const res = await fetch(url);
      if (!res.ok) throw new Error('no txs');
      const json = await res.json();
      setTxs(Array.isArray(json) ? json : []);
    } catch (err) {
      console.warn('loadTxs failed, using mock', err);
      // fallback mock txs:
      const mock = [
        { id: 'tx-1', sender: 'user-77', receiver: 'mock-bitcoin-1', amount: 0.5, currency: 'bitcoin', time: new Date().toISOString() },
        { id: 'tx-2', sender: 'mock-ethereum-2', receiver: 'user-42', amount: 2.3, currency: 'ethereum', time: new Date(Date.now() - 1000 * 60 * 60).toISOString() }
      ];
      setTxs(mock);
    } finally {
      setLoadingTxs(false);
    }
  }

  // bitcoin-only balances for the chart
  const bitcoinBalance = useMemo(() => {
    return wallets.reduce((acc, w) => acc + (w.currency === 'bitcoin' ? Number(w.balance) || 0 : 0), 0);
  }, [wallets]);

  return (
    <section style={{ display: 'grid', gap: 16, gridTemplateColumns: '1fr 420px', alignItems: 'start' }}>
      <div style={{ background: '#fff', padding: 18, borderRadius: 12, boxShadow: '0 6px 18px rgba(15,23,42,0.06)' }}>
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <div>
            <h2 style={{ margin: 0 }}>Crypto Dashboard</h2>
            <div style={{ color: '#666', marginTop: 6 }}>Manage wallets, view recent activity and quick stats.</div>
          </div>
          <div style={{ textAlign: 'right' }}>
            <div style={{ fontSize: 12, color: '#888' }}>Wallets created</div>
            <div style={{ fontWeight: 700 }}>{wallets.length}</div>
          </div>
        </div>

        <div style={{ marginTop: 16, display: 'flex', gap: 12, alignItems: 'center' }}>
          <div style={{ flex: 1 }}>
            <div style={{ fontSize: 12, color: '#666' }}>Overview</div>
            <div style={{ fontWeight: 700 }}>Bitcoin dashboard</div>
          </div>
          {/* Transactions: navigate to per-user page if a connected user is detected */}
          <Link
            href={userId ? `/uis/crypto/${encodeURIComponent(userId)}` : `/uis/crypto`}
            style={{
              padding: '10px 12px',
              borderRadius: 10,
              border: 'none',
              background: userId ? '#0b74de' : '#ccc',
              color: '#fff',
              textDecoration: 'none',
              display: 'inline-flex',
              alignItems: 'center',
              justifyContent: 'center',
              pointerEvents: userId ? 'auto' : 'none',
              opacity: userId ? 1 : 0.7
            }}
            aria-disabled={!userId}
          >
            Transactions
          </Link>
        </div>

        {msg && (
          <div style={{ marginTop: 12, padding: 10, borderRadius: 8, background: msg.type === 'error' ? '#ffecec' : msg.type === 'warn' ? '#fff8e1' : '#e6ffef', color: msg.type === 'error' ? '#a33' : '#064' }}>
            {msg.text}
          </div>
        )}

        <div style={{ marginTop: 18 }}>
          <h3 style={{ margin: '0 0 8px 0' }}>Bitcoin overview</h3>
          <div style={{ display: 'flex', gap: 12, alignItems: 'center', marginBottom: 12 }}>
            <div>
              <div style={{ fontSize: 12, color: '#666' }}>Total BTC across wallets</div>
              <div style={{ fontWeight: 700, fontSize: 18 }}>{bitcoinBalance} BTC</div>
            </div>
            <div>
              <SmallBarChart data={[{ label: 'BTC', value: bitcoinBalance }]} />
            </div>
          </div>
        </div>
      </div>

      {/* Right column: quick stats & recent txs */}
      <aside style={{ position: 'sticky', top: 20 }}>
        <div style={{ background: '#fff', padding: 16, borderRadius: 12, boxShadow: '0 6px 18px rgba(15,23,42,0.06)' }}>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'baseline' }}>
            <h4 style={{ margin: 0 }}>Quick stats</h4>
            <div style={{ fontSize: 12, color: '#888' }}>{wallets.length} wallets</div>
          </div>

          <div style={{ marginTop: 12 }}>
            <SmallBarChart data={[{ label: 'BTC', value: bitcoinBalance }]} />
            <div style={{ marginTop: 10, fontSize: 13, color: '#666' }}>
              Total balance for Bitcoin (simple sum of your bitcoin wallets).
            </div>
          </div>

          <div style={{ marginTop: 14, display: 'flex', gap: 8 }}>
            <button onClick={() => { setTxModeAll(false); loadTxs(10); }} style={{ padding: '8px 10px', borderRadius: 8, border: 'none', background: !txModeAll ? '#0b74de' : '#f0f4f8', color: !txModeAll ? '#fff' : '#0366d6' }}>
              Last txs
            </button>
            <button onClick={() => { setTxModeAll(true); loadTxs(100); }} style={{ padding: '8px 10px', borderRadius: 8, border: 'none', background: txModeAll ? '#0b74de' : '#f0f4f8', color: txModeAll ? '#fff' : '#0366d6' }}>
              All txs
            </button>
            <button onClick={() => loadTxs(txModeAll ? 100 : 10)} style={{ marginLeft: 'auto', padding: '8px 10px', borderRadius: 8, border: 'none', background: '#eee' }}>
              Refresh
            </button>
          </div>

          <div style={{ marginTop: 12 }}>
            {loadingTxs ? (
              <div>Loading transactions…</div>
            ) : txs.length === 0 ? (
              <div style={{ color: '#777' }}>No transactions to show.</div>
            ) : (
              <ul style={{ paddingLeft: 12, maxHeight: 420, overflow: 'auto' }}>
                {txs.map((t) => (
                  <li key={t.id} style={{ marginBottom: 10, fontSize: 13 }}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', gap: 10 }}>
                      <div style={{ color: '#333' }}>
                        <strong style={{ fontSize: 13 }}>{t.amount} {t.currency}</strong>
                        <div style={{ color: '#666', fontSize: 12 }}>{t.sender} → {t.receiver}</div>
                      </div>
                      <div style={{ color: '#888', fontSize: 12 }}>{t.time ? new Date(t.time).toLocaleString() : '—'}</div>
                    </div>
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>

        <div style={{ marginTop: 12, background: '#fff', padding: 12, borderRadius: 12, boxShadow: '0 6px 18px rgba(15,23,42,0.04)' }}>
          <h5 style={{ margin: 0 }}>Helpful tips</h5>
          <ul style={{ marginTop: 8, paddingLeft: 18, color: '#666', fontSize: 13 }}>
            <li>This dashboard shows Bitcoin totals and transactions. Use the Transactions link to open the per-user page to create accounts / send BTC.</li>
            <li>If API is unavailable the UI will fall back to mock data so you can preview flows.</li>
          </ul>
        </div>
      </aside>
    </section>
  );
}